          _createStaticVNode("<h1 data-v-4c35b50e>Mifflin-St. Jeor Formula: Short Overview</h1><ol data-v-4c35b50e><li data-v-4c35b50e><strong data-v-4c35b50e>Year of Discovery</strong><p data-v-4c35b50e>Developed and published in <strong data-v-4c35b50e>1990</strong> by Mifflin and St. Jeor.</p></li><li data-v-4c35b50e><strong data-v-4c35b50e>Purpose</strong><p data-v-4c35b50e>Estimates your <strong data-v-4c35b50e>Basal Metabolic Rate (BMR)</strong>—the calories burned at rest.</p></li><li data-v-4c35b50e><strong data-v-4c35b50e>Equations</strong><p data-v-4c35b50e><strong data-v-4c35b50e>Men:</strong> BMR = (10 × weight in kg) + (6.25 × height in cm) - (5 × age in years) + 5</p><p data-v-4c35b50e><strong data-v-4c35b50e>Women:</strong> BMR = (10 × weight in kg) + (6.25 × height in cm) - (5 × age in years) - 161</p></li><li data-v-4c35b50e><strong data-v-4c35b50e>Activity Factor (TDEE)</strong><p data-v-4c35b50e> Multiply your BMR by one of the following multipliers to estimate your <strong data-v-4c35b50e>Total Daily Energy Expenditure (TDEE)</strong>: </p><ul data-v-4c35b50e><li data-v-4c35b50e><strong data-v-4c35b50e>Sedentary (1.2):</strong> Little or no exercise, desk job</li><li data-v-4c35b50e><strong data-v-4c35b50e>Lightly Active (1.375):</strong> Light exercise 1–3 days/week (e.g., short walks, yoga)</li><li data-v-4c35b50e><strong data-v-4c35b50e>Moderately Active (1.55):</strong> Moderate exercise 3–5 days/week (e.g., regular gym workouts, sports) </li><li data-v-4c35b50e><strong data-v-4c35b50e>Very Active (1.725):</strong> Hard exercise 6–7 days/week (e.g., intense daily training, physically demanding job) </li><li data-v-4c35b50e><strong data-v-4c35b50e>Extra Active (1.9):</strong> Very hard exercise or training twice a day (e.g., athletes, highly physical occupations) </li></ul></li><li data-v-4c35b50e><strong data-v-4c35b50e>Accuracy &amp; Variation</strong><p data-v-4c35b50e> Generally accurate within ±10%. Individual factors—like muscle mass, health conditions, and genetics—may shift actual needs. </p></li><li data-v-4c35b50e><strong data-v-4c35b50e>Direct Calorimetry</strong><p data-v-4c35b50e> For those who want the <strong data-v-4c35b50e>most accurate results</strong> of their energy expenditure, <strong data-v-4c35b50e>direct calorimetry</strong> is available. In this method, you stay in an airtight chamber where sensors measure the <strong data-v-4c35b50e>heat</strong> your body produces. </p><p data-v-4c35b50e> While this method is highly accurate, it is <strong data-v-4c35b50e>costly</strong>, requires strict environmental control, and involves prolonged stays in the chamber—making it impractical for everyday use. </p></li></ol>", 2)
