          _createStaticVNode("<h1 data-v-4c35b50e>Формула Міффліна-Сан-Жеора: короткий огляд</h1><ol data-v-4c35b50e><li data-v-4c35b50e><strong data-v-4c35b50e>Рік відкриття</strong><p data-v-4c35b50e>Розроблена та опублікована у <strong data-v-4c35b50e>1990</strong> році Міффліном та Сан-Жеором.</p></li><li data-v-4c35b50e><strong data-v-4c35b50e>Призначення</strong><p data-v-4c35b50e> Оцінює ваш <strong data-v-4c35b50e>базальний рівень метаболізму (BMR)</strong> — кількість калорій, що спалюються в стані спокою. </p></li><li data-v-4c35b50e><strong data-v-4c35b50e>Формули</strong><p data-v-4c35b50e><strong data-v-4c35b50e>Чоловіки:</strong> BMR = (10 × вага в кг) + (6.25 × зріст у см) - (5 × вік у роках) + 5</p><p data-v-4c35b50e><strong data-v-4c35b50e>Жінки:</strong> BMR = (10 × вага в кг) + (6.25 × зріст у см) - (5 × вік у роках) - 161</p></li><li data-v-4c35b50e><strong data-v-4c35b50e>Фактор активності (TDEE)</strong><p data-v-4c35b50e> Помножте ваш BMR на один із наведених нижче множників, щоб визначити ваші <strong data-v-4c35b50e>загальні добові витрати енергії (TDEE)</strong>: </p><ul data-v-4c35b50e><li data-v-4c35b50e><strong data-v-4c35b50e>Малорухливий (1.2):</strong> Мінімальні або відсутні фізичні навантаження, офісна робота</li><li data-v-4c35b50e><strong data-v-4c35b50e>Слабо активний (1.375):</strong> Легкі тренування 1–3 дні/тиждень (наприклад, короткі прогулянки, йога) </li><li data-v-4c35b50e><strong data-v-4c35b50e>Помірно активний (1.55):</strong> Помірні тренування 3–5 днів/тиждень (наприклад, регулярні заняття в спортзалі, спортивні ігри) </li><li data-v-4c35b50e><strong data-v-4c35b50e>Дуже активний (1.725):</strong> Інтенсивні тренування 6–7 днів/тиждень (наприклад, щоденні виснажливі заняття, фізично вимоглива робота) </li><li data-v-4c35b50e><strong data-v-4c35b50e>Надзвичайно активний (1.9):</strong> Дуже важкі тренування або заняття двічі на день (наприклад, спортсмени, вкрай фізично напружені професії) </li></ul></li><li data-v-4c35b50e><strong data-v-4c35b50e>Точність та відхилення</strong><p data-v-4c35b50e> Зазвичай формула дає результат із похибкою близько ±10%. Індивідуальні чинники — такі як м’язова маса, стан здоров’я та генетика — можуть змінювати фактичні потреби. </p></li><li data-v-4c35b50e><strong data-v-4c35b50e>Пряма калориметрія</strong><p data-v-4c35b50e> Для тих, хто прагне отримати <strong data-v-4c35b50e>найточніші результати</strong> енергетичних витрат, існує метод <strong data-v-4c35b50e>прямої калориметрії</strong>. Під час нього ви перебуваєте у герметичній камері, де сенсори вимірюють <strong data-v-4c35b50e>тепло</strong>, що виділяє ваше тіло. </p><p data-v-4c35b50e> Цей метод надзвичайно точний, проте <strong data-v-4c35b50e>дорогий</strong>, вимагає суворого контролю навколишнього середовища і передбачає тривале перебування в камері, що робить його непрактичним для щоденного використання. </p></li></ol>", 2)
